import React from 'react';
import './Hot.css'
import {NavLink} from 'react-router-dom'
import querystring from 'query-string'

let Hot = (props) => {
    let {hotList} = props.data;
    return <div className="tj_c">
        <ul>
            {
                hotList.map((item,index)=>(
                    <li key={item.goodId}>
                        <div className="img"><NavLink to={{
                                        pathname:'/detail/'+item.goodId,
                                        search:querystring.stringify({
                                            dataName:"hotList"
                                        })
                                    }}><img src={item.src} style={{width:'7rem'}}/></NavLink></div>                                        
                        <div className="name">{item.name}</div>
                        <div className="price">
                            <font>ï¿¥<span>{item.price}</span></font> &nbsp;{item.size}
                        </div>
                    </li>
                ))
            }
        </ul>
    </div>
}

export default Hot