import React from 'react';
import './HotTop.css'
import {NavLink} from 'react-router-dom'
import querystring from 'query-string'

let HotTop = (props) => {
    let {hotTopList} = props.data
    return <div className="fresh_mid">
        <ul>
            {
                hotTopList.map((item,index)=>(
                    <li key={item.goodId}>
                        <div className="name">{item.name}</div>
                        <div className="price">æä½ä»·{item.price}</div>
                        <div className="img"><NavLink to={{
                                        pathname:'/detail/'+item.goodId,
                                        search:querystring.stringify({
                                            dataName:"hotTopList"
                                        })
                                    }}><img src={item.src} style={{width:'5rem'}}/></NavLink></div>
                    </li>
                ))
            }
        </ul>
    </div>
}

export default HotTop