import React from 'react';
import './TimeSale.css'
import {NavLink} from 'react-router-dom'
import querystring from 'query-string'

let TimeSale = (props) => {
    let {timeList} = props.data;
    return <ul className="tm_c">
                {
                    timeList.map((item,index)=>(
                        <li className="sell_1" key={item.goodId}>
                            <div className="s_img"><NavLink to={{
                                        pathname:'/detail/'+item.goodId,
                                        search:querystring.stringify({
                                            dataName:"timeList"
                                        })
                                    }}><img src={item.src} style={{width:'26.6.rem'}}/></NavLink></div>
                            <div className="s_price">ï¿¥<span>{item.price}</span></div>
                            <div className="s_name">
                                <h2><NavLink to={{
                                        pathname:'/detail/'+item.goodId,
                                        search:querystring.stringify({
                                            dataName:"timeList"
                                        })
                                    }}>{item.name}</NavLink></h2>
                                åè®¡æ¶ï¼<span>1200</span> æ¶ <span>30</span> å <span>28</span> ç§
                            </div>
                        </li>
                    ))
                }
            </ul>
}

export default TimeSale