import React from 'react';
import './Home.css'
import List from '../common/List';
import Nav from './Nav' 
import Title from '../common/Title';
import Hot from './Hot';
import TimeSale from './TimeSale';
import HotTop from './HotTop';
import Title1 from '../common/Title1';
import Swipe from '../common/Swipe'

import * as types from '../store/type'
import { connect } from 'react-redux';
import { asyncActionPro } from '../store/anyscAction';
import QueueAnim from 'rc-queue-anim';

class Home extends React.Component{
    componentDidMount(){
        this.props.getList({goodName:'list'},types.UPDATE_LIST)
        this.props.getList({goodName:'hotList'},types.UPDATE_HOTLIST)
        this.props.getList({goodName:'hotTopList'},types.UPDATE_HOTTOPLIST)
        this.props.getList({goodName:'timeList'},types.UPDATE_TIMELIST)
    }
    render(){
        return <QueueAnim animConfig={{opacity:[1, 0]}} duration="3000">
                    <div key={1} className="home">
                        <Swipe data={{src1:"/image/ban1.jpg",src2:"/image/ban2.jpg",src3:"/image/ban3.jpg"}}></Swipe>
                        <Nav></Nav>
                        <Title data={{src:"/image/h_icon.png",title:"ç­é¨æ¨è",path:"/timepro"}}></Title>
                        <Hot data={{hotList:this.props.hotList}}></Hot>
                        <Title data={{src:"/image/t_icon.png",title:"éæ¶ç¹å",path:"/timepro"}}></Title>
                        <TimeSale data={{timeList:this.props.timeList}}></TimeSale>
                        <Title1 data={{src:"/image/mban.jpg"}}></Title1>
                        <Title data={{src:"/image/ht_icon.png",title:"ç­é¨ä¸»é¢",path:"/product"}}></Title>
                        <HotTop data={{hotTopList:this.props.hotTopList}}></HotTop>
                        <Title1 data={{src:"/image/mban1.jpg"}}></Title1>
                        <Title data={{src:"/image/l_icon.png",title:"çä½ åæ¬¢",path:"/product"}}></Title>
                        <List data={{list:this.props.list}}></List>
                    </div>
                </QueueAnim>
    }
}

let MapStateToProps = (state)=>({
    list:state.list,
    hotList:state.hotList,
    hotTopList:state.hotTopList,
    timeList:state.timeList
})

let MapDispatchToProps = (dispatch)=>({
    getList:(option,type)=>{asyncActionPro(
        option,
        dispatch,
        type
    )}
})

export default connect(
    MapStateToProps,
    MapDispatchToProps
)(Home)